import React from 'react';

function Home() {
  return (
    <div className="text-center">
      <h1>Bine ai venit n judeul Vlcea!</h1>
      <p className="lead mt-3">
        Exploreaz cele mai frumoase atracii turistice, cazri pitoreti i mprtete amintiri din cltoriile tale.
      </p>
      <img
        src="/images/judetul_valcea.jpg"
        className="img-fluid mt-4"
        alt="Valcea"
        style={{ maxHeight: '500px', borderRadius: '10px' }}
/>
    </div>
  );
}

export default Home;
